.wizard_h .shade-main{
    position: absolute;
    z-index: 2015;
    left:-2%;
    top:-5%;
    height:103%;
    width:104%;
    /* background-color: rgba(0, 0, 0, 0.5); */
    opacity: 0.3;
    background: #000000;
  }
.wizard_h{
    width: -webkit-fill-available;
    width: -moz-available;
}
.wizard_h header{
    height:max-content;
}
.wizard_h .x_stepLabel{
    display:flex;
    justify-content: center;
    margin: 0 60px;
    position: relative;
}
.wizard_h .x_stepLabel2{
    display: flex;
    justify-content: center;
    margin: 0 60px 0 16px;
    position: relative;
}
.wizard_h .y_stepLabel{
    min-width: 250px;
    max-width: 250px;
    display:flex;
    flex-direction: column;
    height:100%;
    /* width: max-content; */
    padding:0 10px;
    overflow: auto;
    overflow-x: hidden;
    max-height: 75vh;
}
.wizard_h .x_eachStep{
    /* min-width: 106px; */
    display: grid;
    width: -webkit-fill-available;
    width: -moz-available;
    /* width: 240px; */
    grid-template-columns: min-content auto;
    position: relative;
    grid-template-rows: 40px auto;
    padding:10px 0 0;
}
.wizard_h .x_eachStep2{
    display: inline-flex;
    /* min-width: 106px; */
    width: -webkit-fill-available;
    width: -moz-available;
    padding:16px 0;
    position:relative;
}
.wizard_h .y_eachStep{
    /* min-width: 125px; */
    display: grid;
    grid-template-columns: min-content auto;
    /* display: flex; */
    position: relative;
}
.wizard_h .x_stepLabel2 .stepName{
    /* align-self: center; */
    max-width: 11em;
    margin: 5px 10px ;
    overflow-wrap: anywhere;
    display: flex;
    align-self: center;
}
.wizard_h .x_stepLabel .stepName{
    /* position: absolute; */
    left: 0;
    bottom: 0;
    /* transform: translate(0,-50%); */
    max-width: 11em;
    /* height: -webkit-fill-available; */
    width: max-content;
    /* top: 0; */
}
.wizard_h .x_stepLabel .stepName_style3{    
    transform: translate(0,0%);
    left:50px;
    /* top: -35px; */
}
.horizontalSubsteppers{
    display: flex;
    justify-content: space-evenly;
}
.horizontalSubsteppers .y_horizontalSubDots{
    position: absolute;
    z-index: 2;
    top:13px;
}
.horizontalSubsteppers .y_horizontalSubDots span{
    width: 8px !important;
}
.wizard_h .stepName .otherSteps{
    position: relative;
    /* right:24px; */
    /* transform: translate(-33%, 0); */
}
.wizard_h .stepName .lastStep{
    position: relative;
    /* left:0;
    transform: translate(-33%, 0); */
}
.wizard_h .y_eachStep .stepName{
    margin: 0;
    margin-left: 21px;
    align-self: center;    
    overflow-wrap: anywhere;
    /* max-height:1em; */
}
.wizard_h .y_stepLabel_mobile .circle{
    height:24px;
    width:24px ;
    line-height: 18px;
    margin:0 10px;
}
.wizard_h .circle{
    height:32px;
    width:32px ;
    border-radius:50%;
    border:2px solid #0072C6;
    color:#0072C6;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 29px;
    align-items: center;
}
.wizard_h .circleSmall{
    height:8px;
    width:8px !important;
    border-radius:50%;
    /* border:2px solid #0072C6; */
    color:#0072C6;
    background-color: #0072c6;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 29px;
    align-items: center;
}
.wizard_h .x_stepLabel .activeSubstepLine_x{
    margin:8px 0 !important;
}
.wizard_h .x_stepLabel .line{
    border-top: 1px solid #C4C4C4;
    width:100%;
    height:1px;
    /* margin-top:1.3em; */
    margin:17px 0;
    position: relative;
}
.wizard_h .y_eachStep .line{
    border-left: 1px solid #C4C4C4;
    height:50px;
    margin-top: 0;
}
.wizard_h main{
    min-height:200px;
    /* max-height:calc(75vh - (60px + 41px + 75px)); */
    width: 100%;
    /* background: #F8F8F8;   */
    border-radius: 0px 0px 2px 2px;
    border-bottom:0 ;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 14px 0;
    padding-bottom:5em;
}
.wizard_h .wizard_h_footer{
    height:50px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    border-top: 1px solid #e4e4e4;
    width: 100%;
    /* border-top:0; */
    box-shadow: 0px -3px 6px #00000015;
    
    /* display: grid;
    grid-template-columns: 50px 70px auto 40px 40px; */    
    display: flex !important;
    position: relative !important;
    justify-content: space-between;
    column-gap: 16px;
    align-items: center;
    padding:16px;
}
.wizard_h .wizard_h_footer button{
    margin:0 10px;
    font-size: 14px !important;
    border-radius: 2px !important;
}
.wizard_h .y_alignment{
    display:flex;
}
.y_stepLabel .y_substep2{
    display:flex;
    flex-direction: column;
    color:#606060;
    margin:5px 0 0;
    /* width:211px; */
}
.y_stepLabel .y_substep2 span{
    cursor:pointer;
    padding:7.5px 0;    
    font-size: 100;
    overflow-wrap: anywhere;
}
.wizard_h .x_canvas_sup{
    display: grid;
    grid-template-columns: 211px auto ;
    width: 100%;
}
.wizard_h .x_substep_main{
background: #F0F0F0 ;
border: 1px solid #C4C4C4;
display:flex;
flex-direction: column;
font-size: 13px;
align-items: flex-start;
padding: 25px 2.5em;
min-width: 211px;
}
.wizard_h .y_stepLabel_mobile .x_substep_main{
    background: #fff ;
    /* border: 1px solid #C4C4C4; */
    border:none;
    display:flex;
    flex-direction: column;
    font-size: 13px;
    align-items: flex-start;
    padding: 16px 2.5em;
}
.wizard_h .y_substep1Main{    
display:flex;
flex-direction: column;
font-size: 13px;
align-items: flex-start;
position: relative;
left:-1.55em;
padding:3.5em 0 0;
}
.wizard_h .y_substep1Main b{
    margin-left: 1em;
}
.wizard_h .x_substep_each{
    /* display:grid;
    grid-template-columns: 10px auto; */
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.wizard_h .x_substep_each b{
    position: absolute;
    left: 2em;
    top:-7px;
    overflow-wrap: anywhere;
    width: 145px;
}
.wizard_h  .x_substep_each span{
    border: 1px solid #c4c4c4;
    height: 9px;
    width: 9px;
    border-radius:50%;
}

.wizard_h  .x_substep_each line{
    border-left:1px solid #c4c4c4;
    width: 0px;
    /* height:3em; */
}

